import { Container, NineSlicePlane, Sprite, Text, Texture } from "pixi.js";

export class BonusIntroLetter extends Container {
    private txt: Text;
    constructor() {
        super();
        const size = 140;
        const back = new NineSlicePlane(Texture.from("top_back.png"), 46, 50, 46, 50);
        back.width = size;
        back.height = size;
        back.pivot.set(size*0.5, size*0.5);
        this.addChild(back);

        this.txt = new Text("", {fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 60, fill: 0x000000, align: "center", fontWeight: "bold"});
        this.txt.anchor.set(0.5, 0.5);
        this.addChild(this.txt);
    }
    public set letter(l: string) {
        if (l === " ") l = "_";
        this.txt.text = l;
    }
}